import { createTheme, responsiveFontSizes } from "@mui/material";

let theme = createTheme({
  palette: {
    primary: {
      main: "#353353",
      light: "#F9F9FF",
    },
    secondary: {
      main: "#ff4c60",
    },
    info: {
      main: "#FFD15C",
    },
    text: {
      primary: "#353353",
    },
  },
});

theme = responsiveFontSizes(theme);
export default theme;
